nk-blue: linear-gradient(\n    54deg,\n    rgba(255, 49, 104, 1) 0%,\n    rgba(0, 165, 255, 1) 100%\n  );\n\n  --gradient-blue-pink: linear-gradient(\n    30deg,\n    rgba(0, 165, 255, 1) 0%,\n    rgba(255, 49, 104, 1) 100%\n  );\n\n  --gradient-ddp: linear-gradient(\n    90deg,\n    rgba(255, 54, 106, 1) 0%,\n    rgba(255, 54, 106, 1) 10%,\n    rgba(251, 255, 0, 1) 10%,\n    rgba(251, 255, 0, 1) 20%,\n    rgba(0, 174, 255, 1) 20%,\n    rgba(0, 174, 255, 1) 30%,\n    rgba(29, 29, 29, 1) 30%\n  );\n\n  --gradient-ddp-small: linear-gradient(\n    90deg,\n    rgba(255, 54, 106, 1) 0%,\n    rgba(255, 54, 106, 1) 7%,\n    rgba(251, 255, 0, 1) 7%,\n    rgba(251, 255, 0, 1) 13%,\n    rgba(0, 174, 255, 1) 13%,\n    rgba(0, 174, 255, 1) 20%,\n    rgba(29, 29, 29, 1) 20%\n  );\n}\n","body,\nhtml {\n  padding: 0;\n  margin: 0;\n  font-family: 'Montserrat', sans-serif;\n  background: var(--white);\n}\n\n* {\n  box-sizing: border-box;\n}\n\nimg {\n  display: flex;\n  width: 100%;\n  height: auto;\n  margin: auto;\n  color: black;\n  font-size: 0.8em;\n  text-transform: uppercase;\n  color: black;\n  font-weight: 600;\n  text-align: center;\n  overflow: hidden;\n\n  align-items: center;\n  justify-content: center;\n}\n\nmain {\n  margin: 40px 0 40px 0;\n}\n\n.wrapper {\n  width: var(--wrapper-width);\n  max-width: var(--max-width);\n}\n\nul {\n  padding: 0;\n  margin: 0;\n}\n\na {\n  text-decoration: none;\n}\n\n@media screen and (max-width: 40em) {\n  .hide-mobile {\n    display: none !important;\n  }\n}\n\n@media screen and (min-width: 40em) {\n  .hide-desktop {\n    display: none !important;\n  }\n}\n","a i {\n  color: var(--light-gray);\n  margin-right: var(--half-margin);\n  transition: color 0.2s ease, transform 0.2s ease;\n  text-shadow: none;\n  /* font-size: 0.9em; */\n}\n\na:hover i {\n  color: var(--dark-gray);\n}\n\n.download:hover i {\n  /* color: var(--highlight-yellow); */\n}\n\n.listen:hover i {\n  /* color: var(--highlight-pink); */\n}\n\n.rss:hover i {\n  color: var(--highlight-pink);\n}\n\n.itunes:hover i {\n  color: var(--highlight-blue);\n}\n",".rich-text {\n  max-width: var(--max-width);\n  font-size: 1em;\n  margin: auto;\n}\n\n.rich-text > :first-child {\n  margin-top: var(--margin);\n}\n\n.rich-text > :last-child {\n  margin-bottom: 0px;\n}\n\n.rich-text.no-margin > :first-child {\n  margin-top: 0px;\n}\n\n.rich-text *::selection {\n  color: var(--dark-gray);\n  background: var(--highlight-soft);\n}\n\n.rich-text *::-moz-selection {\n  color: var(--dark-gray);\n  background: var(--highlight-soft);\n}\n\n.rich-text ul {\n  margin: var(--margin) 0 var(--margin) 0;\n  list-style: none;\n  padding: 0;\n}\n\n.rich-text ul li {\n  font-size: 1em;\n  max-width: var(--max-width);\n  line-height: 1.8em;\n  margin: 0 0 var(--half-margin) 0;\n  box-sizing: border-box;\n  width: 100%;\n}\n\n.rich-text ul li:before {\n  content: '- ';\n}\n\n.rich-text ul li a {\n  font-size: 1em;\n}\n\n.rich-text ul li p {\n  display: inline;\n  font-size: 1em;\n}\n\n.rich-text h1 {\n  width: 100%;\n  text-align: left;\n}\n\n.rich-text h2 {\n  margin: 10px 0 10px 0;\n}\n\n.rich-text h3 {\n  width: 100%;\n}\n\n.rich-text p a {\n  font-size: 1em;\n}\n\n.rich-text p:first-child img {\n  display: none;\n}\n\n.rich-text blockquote {\n  font-style: italic;\n  margin-left: 1.5em;\n  padding-left: 1em;\n  margin-bottom: var(--margin);\n  border-left: 2px solid var(--highlight-blue);\n}\n\n.rich-text blockquote p {\n  color: $gray3;\n  margin: 0;\n}\n\n.rich-text pre {\n  background-color: var(--light-gray);\n  display: block;\n  padding: 1em;\n  overflow-x: auto;\n  margin: var(--margin) auto var(--margin) auto;\n  border-left: 2px solid var(--highlight-blue);\n}\n\n.rich-text pre > code {\n  padding: 0;\n  background-color: transparent;\n  white-space: pre;\n}\n\n.rich-text hr {\n  box-sizing: content-box;\n  height: 0;\n  overflow: visible;\n  border-color: var(--highlight-blue);\n}\n\n.rich-text code {\n  font-size: 0.9em;\n  padding: 0 0.5em;\n  bac